.wrap-adandsocial {
    padding: 15px;
}
.wrap-adandsocial > .col {
    padding: 0px;
}

/*  */
.wrap-deliverance-img {
    margin-right: 20px;
    flex-grow: unset;
    width: 358px;
    height: 358px;
}
.wrap-deliverance-img > img {
    display: block;
    width:358px;
    height: 358px;
}

/* Instagram */
.wrap-su-instagram {
    position: relative;
    height: 358px;
    border: 1px solid #e1e1e1;
    overflow: hidden;
}
.wrap-su-insta-header {
    display: flex;
    position: absolute;
    top: 0px;
    padding: 7px 10px;
    width: 100%;
    background-color: #fff;
    border-bottom: 1px solid #e1e1e1;
    z-index: 999;
    opacity: 0.9;
}
.wrap-su-insta-header:hover {
    opacity: 1;
}
a.su-insta-user-img, .su-insta-prof-link, .wrap-insta-profile-btn { display: flex; flex-direction: column; align-items: center; }
a.su-insta-user-img {
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 20px;
    border: 2px solid #d6249f;
    justify-content: center;
    /* border-image: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%); */
}
a.su-insta-user-img > img {
    width: 100%;
    border: 1px solid #fff;
    border-radius: 50%;
    /* border-image: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%); */
}
.su-insta-prof-link {
    position: relative;
    display: flex;
    /* justify-content: space-between; */
    margin-left: 15px;
    margin-right: 10px;
    padding-top: 4px;
}
.su-insta-username, .su-insta-location {
    position: relative;
    display: flex;
    /* justify-content: space-between; */
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    color: #333;
    line-height: 1;
}
.su-insta-username:hover, .su-insta-location:hover {
    color: #333;
    text-decoration: underline;
}
.su-insta-username {
    padding-bottom: 1px;
    font-size: 16px;
    font-weight: 500;
}
.su-insta-location {
    font-size: 15px;
}

.wrap-insta-profile-btn {
    margin: 0px 0px 0px auto;
}
.wrap-insta-profile-btn > a {
    margin: 0px;
    margin-top: 3px;
    padding: 7px 17px;
}

.su-insta-feed-img {
    /* position: absolute; */
    display: block;
    width: 100%;
    height: 100%;
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
}
.su-insta-feed-img > img {
    width: 100%;
}

.su-insta-bottom {
    position: absolute;
    bottom: 0px;
    padding: 5px 10px;
    width: 100%;
    background-color: #fff;
    border-top: 1px solid #e1e1e1;
    z-index: 999;
    opacity: 0.9;
}
.su-insta-bottom:hover {
    opacity: 1;
}
.su-insta-view-more {
    position: absolute;
    top: 8px;
    font-weight: 500;
}
.su-insta-view-more:hover {
    text-decoration: underline;
}
.su-insta-bottom > i {
    float: right;
    font-size: 30px;
}